.container--header {
  overflow: hidden;
  padding-top: 0;
  padding-bottom: 0;
}

.logo-wrapper {
  max-width: 440px;
  margin: 0 auto;
  padding: 1em 0;
  text-align: center;
}

.logo {
  display: inline-block;
  position: relative;
  width: 100%;
  padding-bottom: 72.7272%; // Logo aspect ratio 320/440
  vertical-align: middle;
  img {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%; /* only required for <img /> */
  }
}

.noscriptMsg {
  padding: 1.4em 2em 1.8em;
  color: $brand-pink;
  font-weight: 700;
  text-align: center;
}