/* Type scale: 18px Major Third
 * http://type-scale.com/?size=18&scale=1.250&webfont=Archivo%2BNarrow&font-family=%27Archivo%20Narrow%27
 */

html {font-size: 1.125em;}

body {
  background-color: white;
  font-family: 'Archivo Narrow', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-weight: 400;
  line-height: 1.45;
  color: #333;
}

p {margin-bottom: 1.3em;}

h1, h2, h3, h4 {
  margin: 1.414em 0 0.5em;
  font-weight: inherit;
  line-height: 1.2;
  font-family: 'Archivo Narrow', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

h1 {
  margin-top: 0;
  font-size: 2.441em;
  text-transform: uppercase;
}

h2 {font-size: 1.953em;}

h3 {font-size: 1.563em;}

h4 {font-size: 1.25em;}

small, .font_small {font-size: 0.8em;}

a {
  color: #cbcbcb;
  transition: color .2s ease-in-out;
  &:hover {
    color: #eaeaea;
  }
}