.tpl-place {
  .container--header {
    text-align: center;
  }
}

.place-title {
  padding: .3em 0 .6em;
  font-size: 1.6em;
  text-transform: uppercase;
}

.container--map {
  text-align: center;
}

.container--map {
  background: $brand-pink;
  h2 {
    margin: .4em 0 .8em;
    color: #fff;
  }
}

#address-info {
  display: none;
  border-top: 2px solid $brand-pink;
  color: $brand-pink;
  background: #fff;
  &.active {
    display: block;
  }
}

#address-info_inner {
  padding: 2em 1em;
}

#address-info_title {
  margin-top: 0;
}

#address-info_quote {
  margin-bottom: 1em;
  font-style: italic;
}

#address-info_navigate {
  display: inline-block;
  margin: 3em 0;
  padding: .4em 2em;
  border: 2px solid $brand-pink;
  color: $brand-pink;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 2px;
  transform: skewY(-15deg)  translate3d( 0, 0, 0);
  transition: all .2s ease-in-out;
  &:hover {
    color: #fff;
    background: $brand-pink;
    transform: skew(0) rotate(-15deg);
  }
}

@media screen and (max-width: 420px) {
  .place-title {
    font-size: 1.2em;
  }
  #address-info_navigate {
    letter-spacing: 0;
  }
}

@media screen and (max-width: 320px) {
  #address-info_navigate {
    padding-left: .8em;
    padding-right: .8em;
    transform: none;
    text-transform: none;
    &:hover {
      transform: none;
    }
  }
}