.container--home-intro {
  padding-top: 0;
  line-height: 1.6;
  font-size: 1.25em;
}

.container--home-content .container-inner {
  padding: 2em 1.4em 1.4em;
  border: 6px double #fff;
}

.places {
  margin: 1em 0;
  padding: 0;
  list-style: none;
}

.place-teaser {
  float: left;
  width: 50%;
  text-align: center;
  a {
    display: inline-block;
    margin: 1em;
    padding: .4em 2em;
    border: 2px solid #fff;
    font-weight: 700;
    text-decoration: none;
    text-transform: uppercase;
    transform: skewY(-15deg);
    transition: all .2s ease-in-out;
    letter-spacing: 2px;
    &:hover {
      color: $brand-pink;
      background: #fff;
      transform: skew(0) rotate(-15deg);
    }
  }
}

@media screen and (max-width: 600px) {
  .place-teaser {
    float: none;
    width: 100%;
    a {
      margin-left: 0;
      margin-right: 0;
    }
  }
}

@media screen and (max-width: 480px) {
  .container--home-intro {
    padding-top: 0;
    font-size: 1em;
  }
}

@media screen and (max-width: 320px) {
  .place-teaser a {
    display: block;
    margin-top: .4em;
    margin-bottom: .4em;
    padding-left: .2em;
    padding-right: .2em;
    letter-spacing: 0;
    font-weight: 400;
    text-transform: none;
    transform: none;
    &:hover {
      transform: none;
    }
  }
}