.container--player {
  overflow: hidden;
}

.player {
  overflow: hidden;
}

.player-left,
.player-right {
  position: relative;
  float: left;
}

.player-left {
  width: 25%;
  text-align: center;
}

.player-right {
  width: 66%;
}

.player-button {
  overflow: hidden;
  position: relative;
  display: inline-block;
  width: 60px;
  height: 64px;
  padding: 0;
  border: 0;
  outline: none;
  background: transparent;
  text-indent: 100%;
  white-space: nowrap;
  cursor: pointer;

  &:before,
  &:after {
    content: '';
    position: absolute;
    display: block;
    bottom: 0;
    left: 0;
    width: 0;
    height: 0;
    border: 0 solid transparent;
    border-width: 16px 0 16px 30px;
    border-left-color: #fff;
    transition: all .1s ease-in-out;
  }

  &:before {
    height: 32px;
  }

  &:after {
    bottom: 16px;
    left: 30px;
  }

  .player--playing & {

    &::before,
    &::after {
      height: 64px;
      border-width: 0 0 0 24px;
    }

    &::after {
      bottom: 0;
      left: 36px;
    }
  }
}

.player-progress {
  z-index: 20;
  position: relative;
  padding: 14px 0;
  transition: opacity .2s ease-in-out;
  &_track {
    border: 2px solid #fff;
    cursor: pointer;
  }

  &_bar {
    width: 0%;
    height: 32px;
    background: #fff;
  }
}


@keyframes move {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 28px 0;
  }
}

.player--loading {
  .player-progress_track {
    background-image: repeating-linear-gradient(-45deg, $brand-pink, $brand-pink 11px, #fff 10px, #fff 20px);
    background-size: 28px 28px;
    animation: move .8s linear infinite;
  }
}

.player-message {
  z-index: 10;
  opacity: 0;
  position: absolute;
  top: -2px;
  left: 0;
  width: 100%;
  text-align: center;
  background: $brand-pink;
  transition: all .2s ease-in-out;
}

.player-message_text {
  padding: 0 0 .5em;
  line-height: 1;
}

.player-message_btns {
  overflow: hidden;
}

.player-message_btn {
  float: left;
  width: 32%;
  margin: 0 0 0 4%;
  padding: .4em .4em;
  border: 2px solid #fff;
  color: #fff;
  background: $brand-pink;
  text-transform: uppercase;
  transition: all .2s ease-in-out;
  line-height: 1;
  &:hover {
    color: $brand-pink;
    background: #fff;
  }
  &:first-child {
    margin-left: 0;
  }
  &--next {
    width: 64%;
  }
}

.player--message {
  .player-progress {
    z-index: 10;
    opacity: 0;
  }
  .player-message {
    z-index: 20;
    opacity: 1;
  }
}

@media screen and (max-width: 480px) {
  .player-left,
  .player-right {
    float: none;
    width: 100%;
  }
  .player-message {
    position: static;
    display: none;
    padding: 1em 0;
  }
  .player--message {
    .player-progress {
      display: none;
    }
    .player-message {
      display: block;
    }
  }
  .player-message_btn,
  .player-message_btn--next {
    width: 48%;
  }
}

@media screen and (max-width: 420px) {
  .player-message_btn {
    width: 43%;
  }
  .player-message_btn--next {
    width: 53%;
  }

}