.container--sponsors {
  color: #333;
  background: #fff;
  text-align: center;
  h2 {
    margin-top: 0;
    color: $brand-pink;
    text-transform: uppercase;
  }
}

.sponsor-row {
  overflow: hidden;
  position: relative;
  margin: 0 -16px;
}

.sponsor {
  float: left;
  display: block;
  width: 28.5714%;
  margin: 8px 2.3809%;
  img {
    max-width: 100%;
    height: auto;
  }
}

.sponsor--wide {
  width: 61.9048%;
}

.container--legal {
  padding: 4em 1em 2em;
}

@media screen and (max-width: 420px) {
  .sponsor-row {
    margin: 0;
  }
  .sponsor,
  .sponsor--wide {
    width: 100%;
    margin: 1em 0 0;
    text-align: center;
  }
}