.map-wrapper {
  display: none;
  position: relative;
  height: 0;
  padding-bottom: 83.772%; /* Intrinsic ratio: 955px/1140px */
  text-align: left;
}

.js .map-wrapper {
  display: block;
}

.map {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.inlinesvg .map {
  display: block;
}

.mapFallback {
  width: 100%;
}

#bomap {
  background: #fff;
  transform: translate3d(0,0,0);
  transform-origin: 0px 0px;
  .water-zebra {
    fill: #f0f0f0;
  }
  .park-zebra {
    fill: #eaeaea;
  }
  .place-marker_circle {
    fill: #fff;
  }
  .place-marker_symbol {
    fill: inherit;
  }
  .features {
    transition: all .4s ease-in-out;
  }
  .railroad {
    fill: none;
    stroke: #cbcbcb;
    stroke-width: 2;
  }
  .road {
    fill: none;
    stroke: #808080;
    stroke-width: 2.5;
    stroke-dasharray: 10,10;
  }
  .highway {
    fill: none;
    stroke: #cbcbcb;
    stroke-width: 8;
  }
  .zoom-hover {
    opacity: 0;
    stroke: $brand-pink;
    stroke-width: 2px;
    fill: rgba(255, 255, 255, 0);
    cursor: zoom-in;
    transition: all .2s;
    &:hover {
      opacity: 1;
    }
  }
  .zoom-mask {
    opacity: 0;
    fill: $brand-pink;
    transform: translate(-1500px, -1400px) scale(5);
    transform-origin: 0px 0px 0px;
    transition: all .4s ease-in-out;
  }
  .control-zoom-out {
    opacity: 0;
    stroke: #fff;
    stroke-width: 2px;
    fill: $brand-pink;
    transform: translate(500px, 500px) scale(5);
    transition: transform .4s ease-in-out, fill .2s ease-in-out;
    &:hover {
      fill: #fff;
    }
  }
  .control-zoom-out_cover {
    fill: #fff;
    transition: fill .2s ease-in-out;
  }
  .control-zoom-out:hover .control-zoom-out_cover {
    fill: $brand-pink;
  }
  .place {
    fill: $brand-pink;
    cursor: pointer;
    transition: fill .2s ease-in-out;
    &:hover {
      fill: #97083A;
    }
    &.active {
      fill: #808080;
      &:hover {
        fill: #404040;
      }
    }
  }
  &.zoom {
    .features {
      transform: translate(-250px,-400px) scale(2);
   }
    .zoom-hover {
      cursor: default;
      transform: translate(-1500px, -1400px) scale(5);
      pointer-events: none;
    }
    .zoom-mask {
      opacity: 1;
      transform: translate(0, 0) scale(1);
    }
    .control-zoom-out {
      opacity: 1;
      cursor: zoom-out;
      transform: translate(0, 0) scale(1);
    }
  }
}

#bomap.active-exzenterhaus #place-exzenterhaus,
#bomap.active-jahrhunderthalle #place-jahrhunderthalle,
#bomap.active-musikzentrum #place-musikzentrum,
#bomap.active-ruhrpark #place-ruhrpark,
#bomap.active-ruhruniversitaet #place-ruhruniversitaet,
#bomap.active-stadtbad #place-stadtbad,
#bomap.active-u35 #place-u35 {
  .place-marker_circle {
    fill: inherit;
  }
  .place-marker_symbol {
    fill: #fff;
  }
}